@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,600|Lora:400);
body {
  margin: 0;
  padding: 0;
  font-family: "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

* {
  list-style: none;
  text-decoration: none;
  padding: 0;
  margin: 0; }

body {
  background-color: #425060;
  font-family: "Open Sans", sans-serif;
  font-size: 1em; }

button {
  background: none;
  border: none;
  font-size: 1em;
  margin: 0 5px; }

.edit {
  color: #425060; }

.delete {
  color: #dd6c54; }

.done {
  color: #4d87a0; }

.clear {
  color: #425060; }

.add {
  color: #425060; }

.container {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  width: 100%;
  height: 100%;
  min-height: 600px;
  max-width: 1200px;
  margin: auto;
  text-align: center;
  background-color: #cacaca;
  color: #425060; }
  .container header {
    width: 100%;
    background-color: #cacaca;
    height: 25vh; }
    .container header h1 {
      position: relative;
      top: 35%;
      color: #dd6c54;
      font-family: Lora, sans-serif;
      font-size: 4em;
      letter-spacing: 2px; }
  .container .content {
    display: flex;
    flex-direction: column;
    height: 100%;
    min-height: 60vh;
    flex: 2 1; }
  .container .todo-input {
    width: 100%;
    flex-basis: 10%;
    margin-top: 30px;
    margin-bottom: 15px;
    display: flex;
    justify-content: center; }
    .container .todo-input label {
      margin-right: 5px;
      font-weight: 600; }
    .container .todo-input input {
      background-color: inherit;
      border: none;
      border-bottom: 1px #425060 solid;
      margin-right: 5px;
      font: inherit; }
  .container .todo-edit {
    font-size: 0.9em;
    width: 90%;
    display: flex;
    flex-wrap: nowrap; }
    .container .todo-edit label {
      margin-right: 5px;
      font-weight: 600; }
    .container .todo-edit form {
      display: flex;
      border-bottom: 1px #425060 solid;
      width: 100%; }
    .container .todo-edit input {
      background-color: inherit;
      border: none;
      margin-right: 5px;
      font: inherit;
      width: 100%; }
  .container .todo-list {
    width: 50%;
    margin: 0 auto;
    max-width: 500px; }
    .container .todo-list li {
      list-style: none;
      text-align: left;
      width: 100%;
      display: flex;
      line-height: 1.6em; }
      .container .todo-list li span {
        display: block;
        width: 90%; }
    .container .todo-list .clear {
      font-size: 0.9em; }
  .container footer {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    bottom: 0;
    width: 100%;
    margin-right: auto;
    background-color: #cacaca;
    height: 15vh; }
    .container footer ul {
      display: flex;
      padding: 0;
      justify-content: space-between;
      width: 20%;
      min-width: 150px; }
      .container footer ul a {
        color: #dd6c54;
        opacity: 0.8; }
      .container footer ul a:hover {
        opacity: 1; }

@media (max-width: 600px) {
  .container .todo-list {
    width: 80%; }
  .clear {
    margin-top: 20px;
    margin-bottom: 20px; } }

p {
  margin: 0; }

#notf {
  -webkit-animation-name: notificattion;
          animation-name: notificattion;
  -webkit-animation-duration: 10s;
          animation-duration: 10s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards; }

@-webkit-keyframes notificattion {
  0% {
    opacity: 0; }
  50% {
    opacity: 0.8; }
  100% {
    opacity: 0;
    display: none; } }

@keyframes notificattion {
  0% {
    opacity: 0; }
  50% {
    opacity: 0.8; }
  100% {
    opacity: 0;
    display: none; } }

